.item{
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin: 10px;
    width: 180px;
    height: 20px;
    border:1px solid gainsboro;
    border-radius: 2px;
    padding: 10px;
    background: gray;
}

 p{
    margin: 0;
    font-family: "fantasy";
    font-size: 18px;
}

 button{
    width: 20px;
    height: 20px;
    border-radius: 60%;
    
}