.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background: silver;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.wrapper{
  background: slategrey;
  padding:10px;
  border-radius: 15%;
  box-shadow: 0 1px 4px 0 rgb(0, 0, 0,0.5);

}
.input-wrapper{
  display: flex;
  justify-content:space-between;
  width: 200px;
  align-items: center;
}
 .input-wrapper input{
  border-radius: 5%;
  border:0;
  background-color: rosybrown;
  width: 160px;
  padding: 6px;
  height: 20px;
  margin: 10px;
  
}
.input-wrapper button{
  width: 20px;
  height: 20px;
  border-radius: 60%;
  outline: none;
  
} 
